import { __ } from '@wordpress/i18n';
import { useState } from '@wordpress/element';
import { BlockControls, InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, PanelRow, RangeControl, ToggleControl, __experimentalUnitControl as UnitControl, ToolbarGroup, ToolbarButton, Button, Dashicon } from '@wordpress/components';

// Settings Components
import Title from '../../Components/Title';
import BDevice from '../../Components/BDevice';
import BColor from '../../Components/BColor';
import Background from '../../Components/Background';
import SpaceControl from '../../Components/SpaceControl';
import { ImageHolderControl } from '../../Components/ImageControl';

import icons from './Const/icons';
import options from './Const/options';
const { generalStyleTabs, pxUnit, perUnit, emUnit } = options;

const Settings = ({ attributes, setAttributes, setActiveIndex }) => {
	const { carousels, carouselOptions, background, padding, isPage, pageSize, pageColor, isPrevNext, prevNextSize, prevNextColor } = attributes;
	const { columns, columnGap, isLoop, speed, autoplayOptions } = carouselOptions;

	const [device, setDevice] = useState('desktop');

	const addCarousel = () => {
		setAttributes({
			carousels: [...carousels, {
				image: { 'id': null, 'url': '', 'alt': '', 'title': '' }
			}]
		});
		setActiveIndex(carousels.length);
	}

	const updateCarousel = (index, type, val, otherType = false) => {
		const newCarousels = [...carousels];

		if (otherType) {
			newCarousels[index][type][otherType] = val;
			setAttributes({ carousels: newCarousels });
		} else {
			newCarousels[index][type] = val;
			setAttributes({ carousels: newCarousels });
		}
		setActiveIndex(index);
	}

	const updateCarouselOption = (type, val, otherType = false) => {
		const newOptions = { ...carouselOptions }

		if (otherType) {
			newOptions[type][otherType] = val;
			setAttributes({ carouselOptions: newOptions });
		} else {
			newOptions[type] = val;
			setAttributes({ carouselOptions: newOptions });
		}
	}

	return <>
		<InspectorControls>
			<TabPanel className='bPlTabPanel' activeClass='activeTab' tabs={generalStyleTabs}>{tab => <>
				{'general' === tab.name && <>
					<PanelBody className='bPlPanelBody help' title={__('Help', 'carousel-block')}>
						<div className='helpItem'>
							<a href='https://bblockswp.com/docs/carousel-block/' target='_blank' rel='noreferrer'><Dashicon icon='book' />{__('Read Documentation', 'carousel-block')}</a>
						</div>

						<div className='helpItem rateUs'>
							<a href='https://wordpress.org/support/plugin/b-carousel-block/reviews/#new-post' target='_blank' rel='noreferrer'>
								<span><Dashicon icon='star-filled' />{__('Would you please rate us?', 'carousel-block')}</span>
								<span>{__('We are new and we need your help to grow!', 'carousel-block')}</span>
							</a>
						</div>
					</PanelBody>


					<PanelBody className='bPlPanelBody addRemoveItems editItem' title={__('Add or Remove Carousels', 'carousel-block')}>
						{carousels?.map((carousel, index) => {
							const { image } = carousel;

							return <PanelBody key={index} className='bPlPanelBody editItem' title={__(`Carousel No ${index + 1}:`, 'carousel-block')} initialOpen={0 !== index ? false : true}>
								<ImageHolderControl label={__('Image:', 'carousel-block')} img={image} onChange={val => updateCarousel(index, 'image', val)} />

								<PanelRow className='itemAction mt20'>
									<Button className='removeItem' label={__('Remove', 'carousel-block')} onClick={e => {
										e.preventDefault();
										setAttributes({ carousels: [...carousels.slice(0, index), ...carousels.slice(index + 1)] });
									}}><Dashicon icon='no' />{__('Remove', 'carousel-block')}</Button>

									<Button className='duplicateItem' label={__('Duplicate', 'carousel-block')} onClick={e => {
										e.preventDefault();
										setAttributes({ carousels: [...carousels, { ...carousels[index] }] });
									}}>{icons.gearSettings}{__('Duplicate', 'carousel-block')}</Button>
								</PanelRow>
							</PanelBody>
						})}

						<div className='addItem'>
							<Button label={__('Add New Carousel', 'carousel-block')} onClick={addCarousel}><Dashicon icon='plus' />{__('Add New Carousel', 'carousel-block')}</Button>
						</div>
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Carousel Options', 'carousel-block')} initialOpen={false}>
						<PanelRow>
							<Title mt='0'>{__('Columns:', 'carousel-block')}</Title>
							<BDevice device={device} onChange={val => setDevice(val)} />
						</PanelRow>
						<RangeControl value={columns[device]} onChange={val => { updateCarouselOption('columns', { ...columns, [device]: val }) }} min={1} max={6} step={1} beforeIcon='grid-view' />

						<Title>{__('Column Gap:', 'carousel-block')}</Title>
						<RangeControl value={columnGap} onChange={val => updateCarouselOption('columnGap', val)} min={0} max={250} step={1} beforeIcon='arrow-right-alt' />

						<ToggleControl label={__('Enable Loop', 'carousel-block')} checked={isLoop} onChange={val => updateCarouselOption('isLoop', val)} />

						<Title>{__('Speed (s):', 'carousel-block')}</Title>
						<RangeControl value={speed} onChange={val => updateCarouselOption('speed', val)} min={0} max={10} step={.05} />
						<small>{__('Smaller speed will be slide faster', 'carousel-block')}</small>

						<ToggleControl label={__('Enable Autoplay', 'carousel-block')} checked={autoplayOptions?.enabled} onChange={val => updateCarouselOption('autoplayOptions', val, 'enabled')} />
						<small>{__('Autoplay will not work in backend', 'carousel-block')}</small>
					</PanelBody>
				</>}


				{'style' === tab.name && <>
					<PanelBody className='bPlPanelBody' title={__('Carousel', 'carousel-block')}>
						<Background label={__('Background:', 'carousel-block')} background={background} onChange={val => setAttributes({ background: val })} defaults={{ color: '#0000' }} />

						<SpaceControl className='mt20' label={__('Padding:', 'carousel-block')} space={padding} onChange={val => setAttributes({ padding: val })} defaults={{ vertical: '10px', horizontal: '10px' }} />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Pagination & Navigation', 'carousel-block')} initialOpen={false}>
						<ToggleControl label={__('Show Pagination', 'skype-button')} checked={isPage} onChange={val => setAttributes({ isPage: val })} />

						{isPage && <>
							<UnitControl className='mt20' label={__('Pagination Size:', 'carousel-block')} labelPosition='left' value={pageSize} onChange={val => setAttributes({ pageSize: val })} units={[pxUnit, perUnit, emUnit]} />

							<BColor label={__('Pagination Color:', 'carousel-block')} value={pageColor} onChange={val => setAttributes({ pageColor: val })} defaultColor='#4527a4' />
						</>}

						<ToggleControl className='mt20' label={__('Show Preview Next Button', 'skype-button')} checked={isPrevNext} onChange={val => setAttributes({ isPrevNext: val })} />

						{isPrevNext && <>
							<UnitControl className='mt20' label={__('Preview Next Size:', 'carousel-block')} labelPosition='left' value={prevNextSize} onChange={val => setAttributes({ prevNextSize: val })} units={[pxUnit, perUnit, emUnit]} />

							<BColor label={__('Preview Next Color:', 'carousel-block')} value={prevNextColor} onChange={val => setAttributes({ prevNextColor: val })} defaultColor='#4527a4' />
						</>}
					</PanelBody>
				</>}
			</>}</TabPanel>
		</InspectorControls>


		<BlockControls>
			<ToolbarGroup className='bPlToolbar'>
				<ToolbarButton label={__('Add New Slide', 'carousel-block')} onClick={addCarousel} ><Dashicon icon='plus' /></ToolbarButton>
			</ToolbarGroup>
		</BlockControls>
	</>;
};
export default Settings;