import { withSelect } from '@wordpress/data';
import { useEffect, useState, useRef, renderToString } from '@wordpress/element';
const $ = jQuery;

import Settings from './Settings';
import Style from './Style';
import Carousel from './Carousel';
import carouselOpt from './carouselOpt';

const Edit = props => {
	const { className, attributes, setAttributes, clientId, isEditorSidebarOpened } = props;
	const { align, carousels, carouselOptions, isPage, isPrevNext } = attributes;

	useEffect(() => { clientId && setAttributes({ cId: clientId.substring(0, 10) }); }, [clientId]); // Set & Update clientId to cId

	const [activeIndex, setActiveIndex] = useState(0);
	const carouselWrapperRef = useRef(null);

	const currentBlock = document.querySelector(`#block-${clientId} .wp-block`);
	useEffect(() => {
		const currentCarousel = document.querySelector(`#bicbCarousel-${clientId} .bicbCarouselWrapper .bicbCarousel`);

		const currentAll = currentCarousel && currentBlock;

		currentAll ? currentBlock.style.display = 'block' : 'initial';
		align === 'full' && currentAll ? currentBlock.style.maxWidth = 'none' : '';
		align === 'wide' && currentAll ? currentBlock.style.maxWidth = 'none' : '';
		align === '' && currentAll ? currentBlock.style.maxWidth = '' : '';

		currentAll && isEditorSidebarOpened ? currentBlock.style.display = 'grid' : '';
	}, [align, isEditorSidebarOpened]);

	// Swiper Scripts
	useEffect(() => {
		// Slider in wrapper
		carouselWrapperRef.current.innerHTML = renderToString(<Carousel attributes={attributes} />);

		// Initialize Slider
		new Swiper(`#bicbCarousel-${clientId} .bicbCarouselWrapper .bicbCarousel`, carouselOpt(attributes, activeIndex, true));

		// Remove duplicate swiper-notification el
		let seen = {};
		$(`#bicbCarousel-${clientId} .swiper-notification`).each(function () {
			let txt = $(this).attr('class');
			if (seen[txt])
				$(this).remove();
			else
				seen[txt] = true;
		});
	}, [currentBlock, carousels, carouselOptions, isPage, isPrevNext]);

	return <>
		<Settings attributes={props.attributes} setAttributes={setAttributes} clientId={clientId} setActiveIndex={setActiveIndex} />

		<div className={className} id={`bicbCarousel-${clientId}`}>
			<Style attributes={attributes} clientId={clientId} />

			<div className='bicbCarouselWrapper' ref={carouselWrapperRef}></div>
		</div>
	</>;
};
export default withSelect((select) => {
	return {
		isEditorSidebarOpened: select('core/edit-post').isEditorSidebarOpened()
	};
})(Edit);